import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import DAODevelopmentScreen from "../screens/dao-development/dao-development-screen";

const DAODevelopmentPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "DAO Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <DAODevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="DAO Development Services | justtrytech.com"
    headTitle="DAO Development Compnay"
    description="Justtrytech is a premier DAO (Decentralized Autonomous Organization) development company based in India. We specialize in delivering top-notch, customized DAO solutions, led by industry experts in DAO development. Our team of seasoned professionals is committed to providing unparalleled DAO development services, ensuring cutting-edge technology and innovation in every project. Explore the future of decentralized governance with JustTryTech's expert DAO development services."
    keywords="DAO development solutions, decentralized autonomous organization development, DAO software development, DAO platform development, blockchain governance solutions, decentralized governance technology, smart contract development for DAO, DAO consulting services, DAO developers India, decentralized organization development, blockchain-based governance solutions, DAO implementation experts."
  ></Seo>
);

export default DAODevelopmentPage;
export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
